@import '../../../utils/variables';
@import '../../../utils/mixins';

.architecture {

  &__content {
    // transform: translate(0, 120%);
    display: flex;
    flex-direction: column;
    transition: all 500ms ease 0ms;
    opacity: 1;

    // &--visible {
    //   transform: translate(0, 0);
    //   opacity: 1;
    // }

    @include on768 {
      display: grid;
      grid-template-areas:
        "title description"
        "title description"
        "link description";
      grid-template-columns: 410px 1fr;
      gap: 56px;
      row-gap: 0;
    }
  
    @include on1280 {
      grid-template-columns: 500px 1fr;
      gap: 80px;
      row-gap: 0;
    }
  }
}
